___CSS_LOADER_EXPORT___.push([module.id, `body {
    font-family: sans-serif;
    text-align: center;
}

.boards {
    display: flex;
    justify-content: center;
    gap: 2rem;
    margin-top: 1rem;
}

.board-area {
    display: flex;
    align-items: center;
    gap: 1rem;
}

.board-column {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.fleet-status {
    list-style: none;
    padding: 0;
    text-align: left;
    min-width: 8rem;
}

.board {
    display: grid;
    grid-template: repeat(10, 2rem) / repeat(10, 2rem);
    gap: 2px;
}

#placement-modal .board {
    margin: 1rem auto 0;
}

.gridCell {
    border: 1px solid #444;
    background-color: #e5e7eb;
}

#board2 .gridCell {
    cursor: pointer;
}

#board2 .disabled .gridCell {
    cursor: not-allowed;
    pointer-events: none;
}

.gridShip {
    background-color: #3b82f6;
}

.gridHit {
    background-color: #ef4444;
}

.gridMiss::after {
    content: "x";
    color: #947805;
    font-size: 1.6rem;
    line-height: 1.8rem;
    display: block;
}

.gridHit,
.gridMiss {
    pointer-events: none;
}

.modal {
    position: fixed;
    inset: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
}

.modal-content {
    background-color: white;
    padding: 1rem;
    border-radius: 4px;
    text-align: left;
}

.hidden {
    display: none;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,uBAAuB;IACvB,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,SAAS;IACT,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA;IACI,gBAAgB;IAChB,UAAU;IACV,gBAAgB;IAChB,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,kDAAkD;IAClD,QAAQ;AACZ;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,sBAAsB;IACtB,yBAAyB;AAC7B;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,mBAAmB;IACnB,oBAAoB;AACxB;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,YAAY;IACZ,cAAc;IACd,iBAAiB;IACjB,mBAAmB;IACnB,cAAc;AAClB;;AAEA;;IAEI,oBAAoB;AACxB;;AAEA;IACI,eAAe;IACf,QAAQ;IACR,oCAAoC;IACpC,aAAa;IACb,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,uBAAuB;IACvB,aAAa;IACb,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA;IACI,aAAa;AACjB","sourcesContent":["body {\n    font-family: sans-serif;\n    text-align: center;\n}\n\n.boards {\n    display: flex;\n    justify-content: center;\n    gap: 2rem;\n    margin-top: 1rem;\n}\n\n.board-area {\n    display: flex;\n    align-items: center;\n    gap: 1rem;\n}\n\n.board-column {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n}\n\n.fleet-status {\n    list-style: none;\n    padding: 0;\n    text-align: left;\n    min-width: 8rem;\n}\n\n.board {\n    display: grid;\n    grid-template: repeat(10, 2rem) / repeat(10, 2rem);\n    gap: 2px;\n}\n\n#placement-modal .board {\n    margin: 1rem auto 0;\n}\n\n.gridCell {\n    border: 1px solid #444;\n    background-color: #e5e7eb;\n}\n\n#board2 .gridCell {\n    cursor: pointer;\n}\n\n#board2 .disabled .gridCell {\n    cursor: not-allowed;\n    pointer-events: none;\n}\n\n.gridShip {\n    background-color: #3b82f6;\n}\n\n.gridHit {\n    background-color: #ef4444;\n}\n\n.gridMiss::after {\n    content: \"x\";\n    color: #947805;\n    font-size: 1.6rem;\n    line-height: 1.8rem;\n    display: block;\n}\n\n.gridHit,\n.gridMiss {\n    pointer-events: none;\n}\n\n.modal {\n    position: fixed;\n    inset: 0;\n    bac